
import { ElasticService } from "../services/elastic.service";
import { User } from "./user";
import config from "./../../config.json";


export class Log {

    eventtype = 'game';
    duration = 0;
    score = 0;
    '@timestamp': string;
    interrupted = false;
    difficulty: number = 2;
    player_id: number;
    region: number;
    education: string;
    sex: number;
    age: number;
    pause_count = 0;
    pause_time = 0;
    
    game_specific_data: any;
    
    host_domain = config.host_domain;



    constructor (
        user: User,
        public game_name: string,     
        public start_time: string, 
        public ui_device?: string,
        public game_version? : string,
        public ui_agent? : any,
        public ui_width? : number,
        public ui_height?: number
        ) { 

        this.player_id = user.player_id || 12;
        
        this.region = user.region || 2;

        this.age = new Date().getFullYear() - user.birthYear || 26
        
        this.sex = user.sex || 1;

        this.education = user.education || 'higher';
            
        }

    interrupt(){
        this.interrupted = true;
    }

    send(){
        let today = new Date()
        this["@timestamp"] = today.toISOString();
        console.log(this);
        ElasticService.postGameLog(this)


    }

    

    
}